.App {
  text-align: left;
}

body{
  background-color: #ecc6a7;
}

a, .nav-link {
  color: #ffb85a!important;
}

a:hover, .nav-link:hover {
  color: #ffd399!important;
  text-decoration: none;
}

h1 {
  line-height: 2;
  font-weight: lighter;
}

iframe {
  height: 360px;
  width: 560px;
  max-width: 100%;
}

.bg-brown {
  background-color: #3c2d21!important;
}

.Title .banner{
  font-size:1.8em;
  padding-top: 120px;
  color: #ffaf28;
  text-shadow: 1px 1px 5px brown;
}

.Title{
  background-image:url('steinway2.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom;
}

.Title, .Footer {
  background-color: rgb(34, 34, 34);
  text-align: center;
  box-sizing: border-box;
  width: 100%;
  line-height: 2;
  justify-content: center;
  align-items: baseline;
  padding: 1em;
  color: #a77754;
}

.Title-Subtitle {
  color: rgba(255, 255, 255, 0.75);
  font-style:italic;
}

#upright-piano {
  max-width: 100%;
  height: auto;
}

.Kuuluisuudet img {
  padding: 10px;
}

.content{
  padding:2em 1em 2em 1em;
}

.navbar-brand {
	color: #ffaf28!important;
}

.Form {
  background-color: #d6a883;
  padding: 10px;
  border:2px solid #a77754;
  border-radius: 5px;
}

.form-control {
  color: #634f39;
  background-color: #cd996f;
  border: 1px solid #40270d;
}

.form-control::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
            color: #634f39;
            opacity: 1; /* Firefox */
}

.form-control:-ms-input-placeholder { /* Internet Explorer 10-11 */
            color: #634f39;
}

.form-control::-ms-input-placeholder { /* Microsoft Edge */
            color: #634f39;
 }

.text-muted {
    color: #634f39!important;
  }

.btn-primary {
  background-color: #3c2d21;
  border-color: #ffb85a;
}

.btn-primary:hover {
  background-color:#704b2c;
  border-color: #ffb85a;
}
